body {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	height: 100vh;
}

#root {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 100%;
}

section {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	width: 50%;
}

li {
	list-style-type: none;
}

.form-group {
	display: -ms-flexbox;
	display: flex;
	width: 100%;
}

.switch {
	position: relative;
	display: inline-block;
	width: 50px;
	height: 20px;
	margin: 0 5px;
}

.switch input {
	display: none;
}

.slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #bdde97;
	-webkit-transition: .4s;
	-o-transition: .4s;
	transition: .4s;
}

.slider:before {
	position: absolute;
	content: "";
	height: 15px;
	width: 15px;
	left: 5px;
	bottom: 3px;
	background-color: white;
	-webkit-transition: .4s;
	-o-transition: .4s;
	transition: .4s;
}

input:focus + .slider {
	-webkit-box-shadow: 0 0 1px #2196F3;
	        box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
	-webkit-transform: translateX(26px);
	-ms-transform: translateX(26px);
	transform: translateX(26px);
}

.slider.round {
	border-radius: 34px;
}

.slider.round:before {
	border-radius: 50%;
}

.toggle {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.toggle__head {
	padding: 0 15px 0 0;
	margin: 0;
	font-weight: bold;
}

.toggle__label {
	color: gray;
	font-size: 12px;
}

.list {
	width: 100%;
	margin: 20px 0 0 0;

}

.list__item {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}

.form-group__btn {
	width: 10%;
	min-width: 50px;
}

.list__close-btn {
	background: transparent;
	margin: 0 0 0 0;
	font-weight: bold;
}

.list__done-btn {
	background: transparent;
	margin: 0 0 0 auto;
}

.list__item--done {
	text-decoration: line-through;
}


